<template>
	<view class="container">
		<uni-forms ref="baseForm" :modelValue="formData">
			<uni-forms-item>
				<uni-easyinput prefixIcon="person" v-model="formData.nickname" placeholder="请输入昵称" />
			</uni-forms-item>
		</uni-forms>
		<button @click="setNickname">保存</button>
	</view>
</template>

<script>
	import {
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				formData: {
					nickname: ''
				},
			}
		},
		onLoad(options) {
			this.formData.nickname = options.nickname == 'undefined' ? '' : options.nickname
		},
		methods: {
			...mapMutations(['setUserInfo']),
			async setNickname() { //修改昵称
				if (this.formData.nickname) {
					let userInfo = uni.getStorageSync('userInfo')
					if(this.formData.nickname !== userInfo.nickname){
						let data = {
							userId: userInfo.id,
							nickname: this.formData.nickname
						}
						const response = await this.$api.user.setNickname(data)
						if (response.data.code == 200) {
							userInfo.nickname = data.nickname
							this.setUserInfo(userInfo)
							uni.showToast({
								title: response.data.message,
								icon: 'success',
								duration: 2000
							});
							let t = setTimeout(res => {
								uni.navigateBack()
								clearTimeout(t)
							}, 500)
						}
					}else{
						uni.navigateBack()
					}
				}else{
					uni.showToast({
						title: '昵称不能为空',
						icon: 'error',
						duration: 2000
					});
				}
			}
		}
	}
</script>

<style lang="scss">
	.container {
		background-color: #fff;
		width: 100%;
		height: 100vh;
		padding: 80upx 50upx 0;

		/deep/.is-input-border {
			border-color: rgb(229, 229, 229) !important;
		}

		/deep/.uniui-clear {
			color: rgb(192, 196, 204) !important;
		}

		/deep/.uni-easyinput__content-input {
			height: 70upx;
		}

		button {
			background-color: #009000;
			color: #fff;

		}
	}
</style>